@use 'variables' as vars;
@import 'mixins';

.card {
    border: 2px solid vars.$main__color_6;
    border-radius: 5px;
    padding: 1.5em;
    width: 100%;

    &__title {
        margin: .3em;
        text-transform: uppercase;

        &:hover {
            color: vars.$main__color_3;
            cursor: pointer;
        }
    }

    &__description {
        margin: .5em;
        text-align: justify;
    }

    &__caption {
        margin: .5em;
        font-style: italic;
    }

    &:hover {
        background-color: vars.$main__color_2;
        cursor: pointer;
    }

    @media (max-width: vars.$media_776) {
        & {
            flex-grow: 1;

            &__title {
                color: vars.$main__color_6;
            }
        }
    }
}